<template>
 <div class="nav">
   <van-tabbar v-model="active" :fixed="fixed" active-color="#FF8D59" >
     <van-tabbar-item icon="wap-home"  v-if="shopShow"> 门店</van-tabbar-item>
     <van-tabbar-item icon="gold-coin-o">收益</van-tabbar-item>
     <van-tabbar-item icon="balance-list-o">订单</van-tabbar-item>
<!--     <van-tabbar-item icon="friends-o">用户</van-tabbar-item>-->
   </van-tabbar>
   <div class="Content">
     <shop v-if="divShow==0"></shop>
     <Earning v-if="divShow==1"></Earning>
     <order v-if="divShow==2"></order>
     <user v-if="divShow==3"></user>
   </div>
 </div>
</template>
<script>
  const shop = () => import("../dataStat/shop/shop.vue");
  const order = () => import("../dataStat/order/order.vue");
  const Earning = () => import("../dataStat/Earnings/Earnings.vue");
  const user = () => import("../dataStat/user/user.vue");
  export default {
    components:{
      shop,
      order,
      Earning,
      user
    },
    data() {
      return {
        active:0,
        divShow:0,
        fixed:true,
        shopShow:true,
        icon: {
          normal: '//img.yzcdn.cn/icon-normal.png',
          active: '//img.yzcdn.cn/icon-active.png'
        }
      }
    },
    created(){
      var that=this;
      let path=window.location.href.split("?")[1];
      if(path!=undefined){
        let pathArr=path.split("&");
        pathArr.forEach(function (item,index) {
          let items=item.split("=");
          if(items[0]=="Token"||items[0]=="token"){
              that.$store.state.token=items[1];
              that.$store.state.token2=items[1];
          }else  if(items[0]=="accountType"){
            that.$store.state.accountType=items[1];
          }else  if(items[0]=="level"){
            that.$store.state.level=items[1];
          }else  if(items[0]=="childAgentCount"){
            that.$store.state.childAgentCount=items[1];
          }else  if(items[0]=="storeManagerCount"){
            that.$store.state.storeManagerCount=items[1];
          }
          // if(items[0]=="Token"||items[0]=="token"){
          //   console.log(items[1])
          //   that.$store.state.token=items[1];
          //   that.$store.state.token2=items[1];
          // }else {
          //   that.$store.state[items[0]]=items[1];
          // }
        })
        if(this.$store.state.childAgentCount=="0"&&this.$store.state.storeManagerCount=="0"){
          this.shopShow=false;
          this.divShow=1;
        }
    }
    },
    mounted(){

    },
    methods:{

    },
    watch:{
      "active":function (data){
         if(this.shopShow==false){
             this.divShow=this.active+1;
         }else {
           this.divShow=this.active;
         }
      }
    }
  }
</script>

<style scoped>
.Content{
   height: 100%;
  width: 100%;
  padding-bottom: 100px;
  box-sizing: border-box;
  overflow: auto;
}
.Content::-webkit-scrollbar {
  display: none;
}
  .nav{
    height: 100%;
  }
</style>



